// WHY FRONT-END
const whyFrontEndOne = "I was looking for a field where I could fuel my creativity and be able to constantly develop - a field that wasnt necessarily bound to one sector. Front-End Development fitted that description pretty darn well."

const whyFrontEndTwo = "I would lie if I said I never questioned my choice, the leap. After three hours down trying to solve a bug that turns out being a spelling mistake in the entry path in the webpack config file, yes, I questioned my choice. Or when, after deployment of a site, I realized I've styled the whole project with 175% zoom in the browser, yes, I questioned my choice. But in the end, when a problem is solved, especially a problem that seemed near impossible, its all worth it."

